.modal.show {
  z-index: 1060 !important; 
}

/* Modal Styles */
.modal-lg {
  max-width: 800px;
}

.modal-content {
  border-radius: 12px;
  border: none;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}

.modal-header {
  background: linear-gradient(135deg, #BFAB86 0%, #E6D7A2 100%);
  color: white;
  border-bottom: none;
  border-radius: 12px 12px 0 0;
  padding: 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal-title {
  display: flex;
  align-items: center;
  font-weight: 600;
  margin: 0;
  order: 1;
}

.modal-title svg {
  color: rgba(255, 255, 255, 0.9);
}

.btn-close {
  filter: invert(1);
  opacity: 0.8;
  order: 2;
}

.btn-close:hover {
  opacity: 1;
}

/* Modal Body */
.modal-body {
  padding: 2rem;
  max-height: 70vh;
  overflow-y: auto;
}

/* Form Sections */
.form-section {
  margin-bottom: 2rem;
  padding: 1.5rem;
  background: #f8f9fa;
  border-radius: 8px;
  border: 1px solid #e9ecef;
}

.section-title {
  color: #495057;
  font-weight: 600;
  margin-bottom: 1.5rem;
  padding-bottom: 0.5rem;
  border-bottom: 2px solid #dee2e6;
}

/* Form Controls */
.form-label {
  font-weight: 500;
  color: #495057;
  margin-bottom: 0.5rem;
}

.form-control,
.form-select {
  border: 1px solid #dee2e6;
  border-radius: 6px;
  padding: 0.75rem;
  font-size: 0.875rem;
  transition: all 0.2s ease;
}

.form-control:focus,
.form-select:focus {
  border-color: #28a745;
  box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.25);
}

.form-control.is-invalid,
.form-select.is-invalid {
  border-color: #dc3545;
  box-shadow: 0 0 0 0.2rem rgba(220, 53, 69, 0.25);
}

.invalid-feedback {
  font-size: 0.75rem;
  color: #dc3545;
  margin-top: 0.25rem;
}

/* Form Grid */
.row {
  margin-left: -0.75rem;
  margin-right: -0.75rem;
}

.col-md-6 {
  padding-left: 0.75rem;
  padding-right: 0.75rem;
}

/* Buttons */
.btn {
  border-radius: 6px;
  font-weight: 500;
  transition: all 0.2s ease;
}

.btn-primary {
  background: linear-gradient(135deg, #2c3e50 0%, #34495e 100%);
  border: none;
}

.btn-primary:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(40, 167, 69, 0.3);
}

.btn-outline-primary {
  border-color: #BFAB86;
  color: #BFAB86;
}

.btn-outline-primary:hover {
  background: #B68A35;
  border-color: #B68A35;
  color: white;
}

/* Modal Footer */
.modal-footer {
  background: #f8f9fa;
  border-top: 1px solid #e9ecef;
  padding: 1.5rem;
  border-radius: 0 0 12px 12px;
}

.modal-footer .btn {
  padding: 0.75rem 1.5rem;
  font-weight: 500;
}

/* Loading States */
.spinner-border-sm {
  width: 1rem;
  height: 1rem;
}

/* Error Display */
.alert {
  border-radius: 8px;
  border: none;
  padding: 1rem;
  margin-bottom: 1.5rem;
}

.alert-danger {
  background: #f8d7da;
  color: #721c24;
  border-left: 4px solid #dc3545;
}

.alert svg {
  color: #dc3545;
}

/* Responsive Design */
@media (max-width: 768px) {
  .modal-lg {
    max-width: 95%;
    margin: 1rem;
  }
  
  .modal-body {
    padding: 1rem;
    max-height: 60vh;
  }
  
  .form-section {
    padding: 1rem;
    margin-bottom: 1.5rem;
  }
  
  .row {
    margin-left: -0.5rem;
    margin-right: -0.5rem;
  }
  
  .col-md-6 {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
  }
  
  .modal-footer {
    padding: 1rem;
  }
}

@media (max-width: 576px) {
  .modal-lg {
    max-width: 100%;
    margin: 0;
    height: 100vh;
  }
  
  .modal-dialog {
    height: 100vh;
    margin: 0;
  }
  
  .modal-content {
    height: 100vh;
    border-radius: 0;
  }
  
  .modal-header {
    border-radius: 0;
  }
  
  .modal-footer {
    border-radius: 0;
  }
  
  .form-section {
    margin-bottom: 1rem;
  }
}

/* RTL Support */
[dir="rtl"] .modal-header {
  text-align: right;
  flex-direction: row-reverse;
}

[dir="rtl"] .modal-header .modal-title {
  order: 2;
}

[dir="rtl"] .modal-header .btn-close {
  order: 1;
}

[dir="rtl"] .modal-title {
  font-family: 'IBM Plex Sans Arabic', sans-serif !important;
}

[dir="ltr"] .modal-title {
  font-family: 'Poppins', sans-serif !important;
}

[dir="rtl"] .modal-footer {
  flex-direction: row-reverse;
}

/* Accessibility */
.form-control:focus,
.form-select:focus,
.btn:focus {
  outline: 2px solid #28a745;
  outline-offset: 2px;
}

/* Custom Scrollbar */
.modal-body::-webkit-scrollbar {
  width: 6px;
}

.modal-body::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 3px;
}

.modal-body::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 3px;
}

.modal-body::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}